import {
  AppBar,
  Autocomplete,
  Button,
  CardMedia,
  Modal,
  Paper,
  TextField,
  Toolbar,
  Typography,
} from "@mui/material";
import React, { useEffect } from "react";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import UploadIcon from "@mui/icons-material/Upload";
import FileDownloadDoneIcon from "@mui/icons-material/FileDownloadDone";
import ImageIcon from "@mui/icons-material/Image";
import ArrowRightAltIcon from "@mui/icons-material/ArrowRightAlt";
import { Stack } from "@mui/system";

import { saveAs } from "file-saver";
import { useState } from "react";
import { BoltSharp } from "@mui/icons-material";
import { type } from "@testing-library/user-event/dist/type";
function Apps() {
  let theme = createTheme({
    palette: {
      primary: {
        main: "#212121",
      },
    },
  });
  useEffect(() => {
    document.title = "converter";
  }, []);
  let [fileSizeError, setFileSizeErorr] = useState(false);
  let [fileSrc, setFileSrc] = useState("");
  let [fileType, setFileType] = useState("jpg");
  let [fileName, setFileName] = useState("");
  let [openWarning, setOpenWarning] = useState(false);
  let [openWarningmsg, setOpenWarningmsg] = useState("");
  let [currentFile, setCurrentFile] = useState("");
  let [blobSrc,SETblobsrc]=useState("")
  let openDialouge = () => {
    let input = document.createElement("input");
    input.type = "file";
    input.accept = ".jpg,.png,.avif,.ico,.jfif,.jpeg";
    input.oninput = inputChange;
    input.click();
  };
  let inputChange = (e) => {
    setFileSizeErorr(false);
    setOpenWarning(false);
    setOpenWarningmsg(``);
    let file = e.target.files[0];
    console.log(file);
    console.log("new version");

    if (
      file.type === "image/png" ||
      file.type === "image/jpg" ||
      file.type === "image/jpeg"||  file.type === "image/avif"
      ||file.type === "image/jfif"
    ) {
      var size = parseFloat(file.size / 1024).toFixed(2);
      if (size > 1024) {
        setFileSizeErorr(true);
        return;
      }
      setCurrentFile(file.type);
      if (file.type == "image/" + fileType) {
        setOpenWarning(true);
        setOpenWarningmsg(
          `the file is alredy in ${fileType} format.please select another format`
        );
        setFileSrc(URL.createObjectURL(file));
        SETblobsrc(new Blob([file], {type: "image/avif;charset=utf-8"}));
        setFileName(file.name.split(".")[0]);
        return;
      }

      // if(){
      //     alert()

      // }
      setFileSrc(URL.createObjectURL(file));
      SETblobsrc(new Blob([file], {type: `image/${fileType};charset=utf-8`}));
      setFileName(file.name.split(".")[0]);

      return true;
    }
    setOpenWarning(true);
    setOpenWarningmsg("please choose a valid image format.");
    return false;
  };
  function download() {
    setOpenWarning(false);
    setOpenWarningmsg("");

    if (
      currentFile !== "" &&
      currentFile !== "image/" + fileType &&
      (fileType !== null || undefined || "")
    ) {
      saveAs(blobSrc, `${fileName}.${fileType}`);
      return;
    }
    setOpenWarning(true);
    setOpenWarningmsg(
      fileType == null || undefined || ""
        ? "select a proper format."
        : `the file is alredy in ${fileType} format.please select another format`
    );
  }
  function fileTypeChange(_, type) {
    setOpenWarning(false);
    setOpenWarningmsg("");
    setFileType(type);
    if (currentFile !== "" && currentFile !== "image/" + type) {
      setFileType(type);
      return;
    }
    if (fileSrc !== "") {
      setOpenWarning(true);
      setOpenWarningmsg(
        `the file is alredy in ${type} format.please select another format`
      );
      setFileType(type);
    }
  }
  let cancel = () => {
    setOpenWarning(false);
  };

  return (
    <ThemeProvider theme={theme}>
      <>
        <AppBar variant="elevation" color="primary">
          <Toolbar>
            <ImageIcon />
            <ArrowRightAltIcon />
            <ImageIcon />
            <Typography ml={1}>converter</Typography>
          </Toolbar>
        </AppBar>
        |
        <Stack alignItems={"center"} height={"100vh"}>
          <Stack
            direction={"row"}
            spacing={1}
            width="100%"
            mt={8}
            justifyContent={"center"}
            flexWrap="wrap"
            rowGap={1}
          >
            <Autocomplete
              onChange={fileTypeChange}
              color="primary"
              sx={{
                width: {
                  xs: "85%",
                  sm: "85%",
                  md: "180px",
                },
              }}
              defaultValue="jpg"
              options={["jpg", "png", "webp", "avif", "jfif", "jpeg", "ico"]}
              renderInput={(props) => <TextField {...props} />}
            />
            <Button
              onClick={openDialouge}
              color={fileSrc !== "" ? "success" : "primary"}
              variant="contained"
              sx={{
                minWidth: "auto",
                width: {
                  xs: "90%",
                  xs: "85%",
                  md: "30%",
                },
              }}
              endIcon={
                fileSrc !== "" ? <FileDownloadDoneIcon /> : <UploadIcon />
              }
            >
              convert
            </Button>
          </Stack>
          {fileSizeError && (
            <Typography
              color="error"
              gutterBottom
              mt={1}
              fontWeight={500}
              fontSize={14}
            >
              free trial only allow to convert below 1mb files.
            </Typography>
          )}
          {fileSrc !== "" && (
            <>
              <CardMedia
                component={"img"}
                sx={{
                  width: {
                    xs: "90%",
                    xs: "85%",
                    md: "45%",
                  },
                  height: 350,
                  mt: 2,
                  objectPosition: "center",
                  objectFit: "cover",
                }}
                src={fileSrc}
              />
              <Button
                onClick={download}
                color="primary"
                variant="contained"
                sx={{
                  width: {
                    xs: "90%",
                    xs: "85%",
                    md: "45%",
                  },
                  height: 40,
                  mt: 1,
                  borderRadius: 2,
                }}
              >
                download
              </Button>
            </>
          )}
        </Stack>
        <Modal
          onClose={() => cancel}
          sx={{
            width: "100vw",
            height: "100vh",
          }}
          open={openWarning}
        >
          <Stack justifyContent={"center"} alignItems="center" height={"100vh"}>
            <Paper
              elevation={3}
              sx={{
                height: "auto",
                width: {
                  xs: "90%",
                  sm: "80%",
                  md: 500,
                  borderRadius: 8,
                },
              }}
            >
              <Typography
                textAlign={"center"}
                gutterBottom
                mt={1}
                textTransform="capitalize"
                letterSpacing={1}
              >
                {openWarningmsg}
              </Typography>
              <Stack
                direction={"row"}
                marginBottom={2}
                mt={1}
                pr={2}
                justifyContent="flex-end"
                spacing={2}
              >
                <Button onClick={cancel}>ok</Button>
              </Stack>
            </Paper>
          </Stack>
        </Modal>
      </>
    </ThemeProvider>
  );
}

export default Apps;
